.loader {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 3px dashed #806b00;
    animation: rotate 1s infinite linear;
    position: relative;
    top: 50px;
    margin: 0px auto;
}

@keyframes rotate {
    from {
        transform: rotate(0deg) scale(1);
    }
    to {
        transform: rotate(360deg) scale(1.4);
    }
}